import React, { Component } from 'react';
import { render } from 'react-dom';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';

import '../stylesheets/app.css';
import {Header} from '../components/Header';
import {Home} from '../components/Home';
import {Fig} from '../components/fig';
import NoMatch from '../containers/NoMatchPage';
import LogoIcon from '../../icons/logo.png';
import {Header2} from '../components/Header2';




class App extends Component {

     static menuItems = [
        {
            path: 'FIG',
            title: 'FIG',
            isDefault: true
        }, {
            path: 'NoMatch',
            title: 'FCF'
        }, {
            path: 'FIG-ML',
            title: 'FIG-ML'
        }
    ];

    render() {
        return (

            <div className="app container-fluid" >
                <div className = "app__content">
                    <div><Header/></div>

                    {/*<div id="header" className="app__header">*/}
                     {/*<Header menuItems={this.constructor.menuItems}/>*/}
                     {/*<p>text </p>*/}
                     {/*</div>*/}

                   <div id="home"><Home/></div>
                    <div id="footer"></div>
                </div>
            </div>
            /*<Router history="" >
                <div className = "app">
                    <div id="header" className="app__header">
                        <Header menuItems={this.constructor.menuItems}/>
                    </div>
                    <div id="home">
                        <Home></Home>

{/!*                        <div id="Router" className="app__content container-fluid" >
                            <Route path="/FIG" component={Fig}/>
                            <Route path = "/FCF" component={NoMatch}/>
                        </div>*!/}
                    </div>
                </div>
            </Router>*/
        );
    }
}

export default App;



