import React,{Component} from 'react';
import ExcelIcon from '../../icons/Excel.png';
import {Button, Table} from "react-bootstrap";
import {CSVLink, CSVDownload} from "react-csv"

export class AllocationTable extends React.Component {

    csvData =[
        ['firstname', 'lastname', 'email'] ,
        ['Ahmed', 'Tomi' , 'ah@smthing.co.com'] ,
        ['Raed', 'Labes' , 'rl@smthing.co.com'] ,
        ['Yezzi','Min l3b', 'ymin@cocococo.com']
    ];

    render() {
        return (
            <div className="container-blue">
                <div >
                    <h5>{this.props.allocationName}</h5>
                    <div className="float-left" >
                        <table><tbody>
                            <tr>
                                <td><Button bsStyle = "primary" bsSize = "large">Prepare Allocations</Button> </td>
                                <td width={20}></td>
                                <td><CSVLink data={this.csvData}>{<img src={ExcelIcon} alt="logotype" />}</CSVLink></td>
                                <td width={60}></td>
                            </tr>
                            <tr><td className="last-run-text">Last Run </td></tr>
                        </tbody></table>
                    </div>

                    <div className="float-left">
                        <table><tbody>
                            <tr>
                                <td><Button bsStyle = "primary" bsSize = "large">Calculate</Button> </td>
                                <td width={20}></td>
                                <td><CSVLink data={this.csvData}>{<img src={ExcelIcon} alt="logotype" />}</CSVLink></td>
                                <td width={60}></td>
                            </tr>
                            <tr><td className="last-run-text">Last Run </td></tr>
                        </tbody></table>
                    </div>
                    <div className="float-left" >
                        <table><tbody>
                            <tr>
                                <td><Button bsStyle = "primary" bsSize = "large">Post</Button></td>
                                <td width={20}></td>
                                <td><CSVLink data={this.csvData}>{<img src={ExcelIcon} alt="logotype" />}</CSVLink></td>
                                <td width={60}></td>
                            </tr>
                            <tr><td className="last-run-text">Last Run </td></tr>
                        </tbody></table>
                    </div>
                </div>


            </div>
        );
    };
}




