import React,{Component} from 'react';
import {Tabs, Tab, LeftTabs} from 'pui-react-tabs';
import {AllocationTable} from "./AllocationTable";
import {Nav,NavItem,Row,Col,TabContainer,TabContent,TabPane} from "react-bootstrap";




export class Home extends Component
{
    render()
    {
        return(
            <div>
                <div id="div-right" className="div-home-right">
                    <p>EDW Last Feed: </p>
                    <p>Lawson Posting</p>
                </div>
                <div id="div-tab">
                    <TabContainer id="left-tabs-example"  defaultActiveKey="second">
                        <table><tbody>
                        <tr>
                            <td>
                                <Nav bsStyle="pills" stacked>
                                    <NavItem eventKey="first">
                                        Home
                                    </NavItem>
                                    <NavItem eventKey="second">
                                        Allocations
                                    </NavItem>
                                </Nav>
                                <div id="buffer"></div>
                            </td>
                            <td>
                                <TabContent animation>
                                    <TabPane eventKey="first">
                                        This Home
                                    </TabPane>
                                    <TabPane eventKey="second">
                                        <AllocationTable allocationName="FIGGEN" />
                                        <AllocationTable allocationName="CORP" />
                                        <AllocationTable allocationName="PCP" />
                                    </TabPane>
                                </TabContent>
                            </td>
                        </tr>
                        </tbody></table>
                    </TabContainer>







                </div>
            </div>
        )
    }
}




